import {getAudioContext} from './shared';
import Tempo from 'music-tempo';

function connectProcessorToSource(source, processor, context) {
	source.connect(processor);
	processor.connect(context.destination);
}

function flattenBuffers(channelBuffers) {
	return [...channelBuffers[0], ...channelBuffers[1]].reduce((acc, curr) => ([...acc, ...curr]), new Float32Array());
}

function processAudioStream(streamEvent, buffer) {
	const channelOne = streamEvent.inputBuffer.getChannelData(0);
	const channelTwo = streamEvent.inputBuffer.getChannelData(1);

	buffer[0].push(channelOne);
	buffer[1].push(channelTwo);

	return buffer;
}

export function getTempo(sampleLength=1) {
	if (!navigator.mediaDevices) return Promise.reject('getUserMedia is not supported on your browser!');

	return new Promise((resolve, reject) => {
		navigator.mediaDevices.getUserMedia({audio: true, video: false})
			.then(stream => {
				const audioContext = getAudioContext();
				const source = audioContext.createMediaStreamSource(stream);
				const processor = audioContext.createScriptProcessor(2048, 2, 2);
				let channelBuffers = [[], []];

				connectProcessorToSource(source, processor, audioContext);
				processor.onaudioprocess = e => channelBuffers = processAudioStream(e, channelBuffers);

				setTimeout(() => {
					audioContext.close()
						.then(() => {
							const tempo = new Tempo(flattenBuffers(channelBuffers), {minBeatInterval: 0.5});
							resolve(tempo);
						})
						.catch(err => reject(`problem closing audioContext connection: ${err}`));
				}, sampleLength * 1000);
			})
			.catch(err => reject(err));
	});
}
