import {getAudioContext} from './shared';

function draw(analyser, dataArray, canvasCtx, width, height) {
	analyser.getByteTimeDomainData(dataArray);

	canvasCtx.fillStyle = '#e6cf8b';
	canvasCtx.fillRect(0, 0, width, height);
	canvasCtx.lineWidth = 2;
	canvasCtx.strokeStyle = '#e05038';
	canvasCtx.beginPath();

	const sliceWidth = width * 1.0 / 800;
	let x = 0;

	dataArray.forEach((e, i) => {
		const v = e / 128.0;
		const y = v * height / 2;

		if (i === 0) {
			canvasCtx.moveTo(x, y);
		} else {
			canvasCtx.lineTo(x, y);
		}

		x += sliceWidth;
	});

	canvasCtx.lineTo(width, height);
	canvasCtx.stroke();
}


function drawFrequencyToCanvas(canvasElement, analyser, dataArray) {
	const canvasCtx = canvasElement.getContext('2d');
	const width = canvasElement.width;
	const height = canvasElement.height;

	canvasElement.setAttribute('width', width);
	canvasCtx.clearRect(0, 0, width, height);

	const drawFunction = () => draw(analyser, dataArray, canvasCtx, width, height);
	return setInterval(drawFunction, 1);
}

export function getWaveform(canvasElement) {
	if (!canvasElement) return Promise.resolve(null);
	if (!navigator.mediaDevices) return Promise.reject('getUserMedia is not supported on your browser!');


	return new Promise((resolve, reject) => {
		navigator.mediaDevices.getUserMedia({audio: true, video: false})
			.then(stream => {
				const context = getAudioContext();
				const source = context.createMediaStreamSource(stream);
				const analyser = context.createAnalyser();

				source.connect(analyser);
				analyser.fftSize = 2048;
				const bufferLength = analyser.frequencyBinCount;
				const dataArray = new Uint8Array(bufferLength);

				const drawInterval = drawFrequencyToCanvas(canvasElement, analyser, dataArray);

				return resolve(drawInterval);

			})
			.catch(err => Promise.reject('error getting wave form'));
	});
}
