import React from 'react';

import leftArm from '../../images/figure_1/left_arm.png';
import rightArm from '../../images/figure_1/right_arm.png';
import leftLeg from '../../images/figure_1/left_leg.png';
import rightLeg from '../../images/figure_1/right_leg.png';

import body from '../../images/shapes/shape_7.png';
import core from '../../images/shapes/shape_6.png';

import styles from './figure_one_styles.css';

function getStyles(tempo) {
	return {'animationDuration': `${tempo > 0 ? (60 / tempo) : 0}s`};
}

const FigureOne = ({tempo}) => (
	<div className={styles.figureOne}>
		<div className={`${styles.imageWrapper} ${styles.body}`} style={getStyles(tempo)}>
			<img src={body} alt=""/>
		</div>
		<div className={`${styles.imageWrapper} ${styles.core}`} style={getStyles(tempo)}>
			<img src={core} alt=""/>
		</div>
		<div className={`${styles.imageWrapper} ${styles.leftArm}`} style={getStyles(tempo)}>
			<img src={leftArm} alt=""/>
		</div>
		<div className={`${styles.imageWrapper} ${styles.rightArm}`} style={getStyles(tempo)}>
			<img src={rightArm} alt=""/>
		</div>
		<div className={`${styles.imageWrapper} ${styles.leftLeg}`} style={getStyles(tempo)}>
			<img src={leftLeg} alt=""/>
		</div>
		<div className={`${styles.imageWrapper} ${styles.rightLeg}`} style={getStyles(tempo)}>
			<img src={rightLeg} alt=""/>
		</div>
	</div>
);

export default FigureOne;
